import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useParams } from 'react-router-dom'

import { UI_MESSAGE_MAPPING } from '@components/constants'
import * as Layout from '@components/frontend/layouts'
import * as Headers from '@components/frontend/headers'
import * as Footers from '@components/frontend/footers'

import * as Books from '@components/frontend/books'
import * as Search from '@components/frontend/search'
import * as Forms from '@components/frontend/forms'
import { DrawerTitle } from '@components/frontend/components'
import { useGetBibleRawQuery } from '@components/services'
import {
    generateRestRangeParam,
    retrieveChapterByParamString,
    validIsQueryWholeChapter,
    retrieveBookIndexBySlug,
    goToVerse,
} from '@assets/js/frontend/utilities'
import { loadRaws } from '@assets/js/frontend/states/dataSlice'

export const Verses = () => {
    const dispatch = useDispatch()
    const stateData = useSelector((state) => state.data)
    const stateGeneral = useSelector((state) => state.general)
    const isAfterReload = useSelector((state) => state.general.isAfterReload)
    const params = useParams()

    const { currentSelection } = stateGeneral
    const { readingQuerys } = stateData
    const { chapterVerseInfo } = stateData.translation?.info
    let currentBookIndex = readingQuerys[0]?.book?.index
    if (!currentBookIndex) {
        const { books1: bookSlugOfParam } = useParams()
        currentBookIndex = retrieveBookIndexBySlug(bookSlugOfParam)
    }

    const bookRaws = stateData.raws.filter((raw) => {
        return raw.book === Number(currentBookIndex)
    })
    const selectedRaws = useSelector((state) => state.selected.raws)

    const isQueryWholeChapter = validIsQueryWholeChapter(
        readingQuerys,
        chapterVerseInfo
    )

    let urlParams = generateRestRangeParam(params)
    if (isAfterReload && readingQuerys.length > 0) {
        urlParams = [
            `${readingQuerys[0].book.slug}${readingQuerys[0].chapter}:${readingQuerys[0].verse}`,
        ]
    }

    const currentChapter = retrieveChapterByParamString(urlParams[0])

    const { data, error, isFetching } = useGetBibleRawQuery({
        ranges: urlParams,
        withPrevChapter: isQueryWholeChapter,
        withNextChapter: isQueryWholeChapter,
    })

    useEffect(() => {
        if (data && Object.keys(data).length > 0) {
            dispatch(loadRaws(data.data))
        }
    }, [data])

    useEffect(() => {
        if (error && Object.keys(error).length > 0) {
            throw new Error(error.data.message)
        }
    }, [error])

    useEffect(() => {
        const { books1: bookSlugOfUrlParam, verse1: chapterVerseOfUrlParam } =
            params
        const urlBookIndex = retrieveBookIndexBySlug(bookSlugOfUrlParam)
        const urlChapter =
            chapterVerseOfUrlParam.match(/(\d{0,3}):?(\d{0,3})/i)[1]

        goToVerse(urlBookIndex, urlChapter)
    }, [params])

    return (
        <Layout.AppContainer>
            <Layout.Top>
                <Headers.Main />
                <Headers.Books />
                <Headers.Selection />
            </Layout.Top>
            <Layout.Body className="-mb-8">
                <Books.List />
                <Search.Results />
                <Layout.RawsContent
                    {...{
                        currentSelection,
                        readingQuerys,
                        bookRaws,
                        chapterVerseInfo,
                        currentChapter,
                        selectedRaws,
                    }}
                />
                <Layout.Drawer name="new-post">
                    <Forms.Posts />
                </Layout.Drawer>
                <Layout.Drawer className="p-2" name="relative-posts">
                    <DrawerTitle>相關文章</DrawerTitle>
                </Layout.Drawer>
                <Layout.Drawer
                    className="p-2 content-start flex-wrap"
                    name="selected-raws"
                >
                    <DrawerTitle>
                        {UI_MESSAGE_MAPPING['selected-verses']}
                    </DrawerTitle>
                    <Layout.Drawer.SelectedRaw />
                </Layout.Drawer>
            </Layout.Body>
            <Layout.Bottom>
                <Footers.Main />
            </Layout.Bottom>
        </Layout.AppContainer>
    )
}
